import Map from 'ol/map';
import View from 'ol/view';
import Projection from 'ol/proj/projection';
import Tile from 'ol/layer/tile';
import OSM from 'ol/source/osm';
import Control from 'ol/control';

class mapBase{
    constructor(){
        this.map =  null;//
        this.view = null;//
        this.bounds =  null;//
        this.baseMap = null;//
        this.mapDivID =   null;//id
        this.projection = null;//

    }
    getMap(){

        return this.map;
    }
}
class mapCreator extends  mapBase{
    constructor(ID){
        super();
        //id
        this.mapDivID = ID;
        //
        this.bounds = [118.71735446343295, 30.927840396965042,
            118.77970605871286, 30.971974273228675];

        //
        this.baseMap = new Tile({
            source: new OSM()
        });;

        //
        this.projection = new Projection({
            code: 'EPSG:4326',
            units: 'degrees',
            axisOrientation: 'neu',
            global: true
        });

        this.view = new View({
            center: [118.74581, 30.94982],
            zoom: 14,
            projection: this.projection
        });
    }
    initMap(htmlID, options){
        this.mapDivID = htmlID;

        for(let key in options){
            !!options[key] && (this[key] = options[key]);
        }
        return this;
    }
    startMap(){
        this.map = new Map({
            layers: [
                this.baseMap
            ],
            target: this.mapDivID,
            controls: Control.defaults({
                attributionOptions: {
                    collapsible: false
                }
            }),
            view: this.view
        });
        this.map.getView().fit(this.bounds, this.map.getSize());

        return this;
    }

    addLayer(layers){
        if(!this.map){
            alert("");
            return;
        }
        this.map.addLayer(layers);

        return this;
    }
}

export default  mapCreator;